.accordion,
.inneraccordion {
    background-color: rgb(252, 252, 252);
    color: #444;
    cursor: pointer;
    padding: 18px;
    width: 100%;
    border: 2px solid #014f78;
    text-align: left;
    outline: none;
    font-size: 15px;
    transition: 0.5s;
    border-radius: 5px;
    margin: 6px 0;
    &:focus {
        outline: none;
    }
    i {
        float: right;
        margin-top: 3px;
        font-size: 20px;
        transition: all 0.1s;
    }
}

.active {
    i {
        transform: rotate(90deg);
    }
}

.active,
.accordion:hover {
    background-color: rgb(243, 243, 243);
    color: #014f78;
    font-weight: bold;
}

.panel {
    display: none;
    background-color: white;
    overflow: hidden;
    .innerpanel {
        display: none;
        background-color: white;
        overflow: hidden;
        padding: 0 18px;
        .description{
          font-size: 15px;
        }
    }
    .get {
        border: 1px solid #61affe;
        color: #0783ff;
        background: rgba(97, 175, 254, .1);
        font-weight: bold;
    }
    .post {
        border: 1px solid #49cc90;
        color: #00af5b;
        background: rgba(73, 204, 144, .1);
        font-weight: bold;
    }
    .delete {
        border: 1px solid #f93e3e;
        color: #fa2525;
        background: rgba(249, 62, 62, .1);
        font-weight: bold;
    }
    .put {
        border: 1px solid #fca130;
        color: #fd900a;
        background: rgba(252, 161, 48, .1);
        font-weight: bold;
    }
}

.custom-btn {
    background: transparent;
    border: 1px solid #014f78;
    background-color: #014f78;
    color: white;
    height: 40px;
    padding: 0 15px;
    border-radius: 3px;
    transition: all 0.5s;
    width: 250px;
    &:hover{
      box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
    }
    &:focus{
      outline: none;
      box-shadow: none;
    }
  
}
textarea{
  border: 1px solid rgb(214, 214, 214);
  border-radius: 3px;
  &::placeholder {
    text-align: right;
  }
}
.field{
  width: 250px;
  padding: 5px;
  border: 1px solid rgb(214, 214, 214);
  border-radius: 3px;
}